import React from 'react';
import { useDispatch } from 'react-redux';
import Swal from 'sweetalert2';
import { startLogin, startRegister } from '../../actions/auth';
import { useForm } from '../../hooks/useForm';
import './login.css';

export const LoginScren = () => {
    const dispatch = useDispatch();

    const [ formLoginValues,handLoginInputChance ] = useForm({
        lEmail:'eulalio@gmail.com',
        lPassword:'123456'    
    });
    
    const [ formRegisterValues,handRegisterInputChance ] = useForm({
        rName:'eulalio',
        rEmail:'eulalio@gmail.com',
        rPassword1:'123456',
        rPassword2:'123456'    
    });

    const{ lEmail,lPassword } = formLoginValues;
    const{ rName, rEmail, rPassword1, rPassword2 } = formRegisterValues;


    const handLogin = (e)=>{
        e.preventDefault();
        
        dispatch( startLogin(lEmail,lPassword) )
    }

    const handRegister = (e)=>{
        e.preventDefault();
        
        if(rPassword1 !== rPassword2){
            Swal.fire('Error','Las contraseñas deben ser iguales','error');
            return;
        }
        
        dispatch( startRegister(rEmail,rPassword1,rName) );
    }

    return (
        <div className="container login-container">
            <div className="row">
                <div className="col-md-6 login-form-1">
                    <h3>Ingreso</h3>
                    <form onSubmit={ handLogin }>
                        <div className="form-group">
                            <input 
                                type="text"
                                className="form-control"
                                placeholder="Correo"
                                name="lEmail"
                                value={ lEmail }
                                onChange={ handLoginInputChance }
                            />
                        </div>
                        <div className="form-group">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Contraseña"
                                name="lPassword"
                                value={ lPassword }
                                onChange={ handLoginInputChance }
                            />
                        </div>
                        <div className="form-group">
                            <input 
                                type="submit"
                                className="btnSubmit"
                                value="Login" 
                            />
                        </div>
                    </form>
                </div>

                <div className="col-md-6 login-form-2">
                    <h3>Registro</h3>
                    <form onSubmit={ handRegister }>
                        <div className="form-group">
                            <input
                                type="text"
                                className="form-control"
                                placeholder="Nombre"
                                name="rName"
                                value={ rName }
                                onChange={ handRegisterInputChance }
                            />
                        </div>
                        <div className="form-group">
                            <input
                                type="email"
                                className="form-control"
                                placeholder="Correo"
                                name="rEmail"
                                value={ rEmail }
                                onChange={ handRegisterInputChance }
                            />
                        </div>
                        <div className="form-group">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Contraseña" 
                                name="rPassword1"
                                value={ rPassword1 }
                                onChange={ handRegisterInputChance }
                            />
                        </div>

                        <div className="form-group">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Repita la contraseña" 
                                name="rPassword2"
                                value={ rPassword2 }
                                onChange={ handRegisterInputChance }
                            />
                        </div>

                        <div className="form-group">
                            <input 
                                type="submit" 
                                className="btnSubmit" 
                                value="Crear cuenta" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}